import React from 'react';
import glamorous from 'glamorous';

import Popup from '../layout/Popup';
import ResponsiveTitle from '../layout/ResponsiveTitle';

const gradient = 'linear-gradient(-135deg, #4041E2 0%, #7474DB 100%)';

const Container = glamorous.div({
  position: 'relative'
});

const MainCircle = glamorous.div({
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'center',
  alignItems: 'center',
  padding: '80px',
  backgroundImage: gradient,
  '@media (min-width: 1200px)': {
    height: '600px',
    width: '600px',
    borderRadius: '300px'
  },
  '@media (min-width: 990px) and (max-width: 1199px)': {
    height: '450px',
    width: '450px',
    borderRadius: '225px'
  },
  '@media (min-width: 769px) and (max-width: 989px)': {
    height: '400px',
    width: '400px',
    borderRadius: '200px',
    marginRight: '2rem',
    marginLeft: 'auto'
  },
  '@media (max-width: 768px)': {
    height: '400px',
    width: '400px',
    borderRadius: '200px',
    marginRight: '2rem',
    marginLeft: 'auto'
  }
});

const MediumCircle = glamorous.div({
  position: 'absolute',
  width: '88px',
  height: '88px',
  borderRadius: '44px',
  backgroundImage: gradient
}, ({ top, right, bottom, left }) => ({
  top,
  bottom,
  left,
  right
}));

const SmallCircle = glamorous.div({
  position: 'absolute',
  width: '44px',
  height: '44px',
  borderRadius: '22px',
  backgroundImage: gradient
}, ({ top, right, bottom, left }) => ({
  top,
  bottom,
  left,
  right
}));

const Circles = () => (
  <Container>
    <div className='relative'>
      <MainCircle className='has-text-centered'>
        <ResponsiveTitle
          desktop='3'
          tablet='5'
          mobile='5'
          className='has-text-white'>
          Becoming a merchant is as easy as 1, 2, 3!
        </ResponsiveTitle>
        <p className='is-hidden-touch has-text-white'>Introducing the new LinQ payments.<br />We take care of the all the difficult parts so that you can focus on receiving payments.</p>
        <p className='is-hidden-desktop has-text-white'>
          All we have to do is just follow the steps and become a merchant instantly!
        </p>
        <hr className='is-invisible is-hidden-touch' />
        <Popup>
          <button className='button is-secondary is-rounded'>Get Started</button>
        </Popup>
      </MainCircle>
    </div>
    <MediumCircle top='-44px' right='-44px' className='is-hidden-touch' />
    <MediumCircle bottom='-44px' right='0px' className='is-hidden-touch' />
    <SmallCircle top='-66px' right='66px' className='is-hidden-touch' />
    <SmallCircle top='66px' right='-66px' className='is-hidden-touch' />
  </Container>
);

export default Circles;