import React from 'react';

const ResponsiveTitle = ({ mobile=6, tablet=6, desktop=6, children, className, GlamorousComponent=null }) => GlamorousComponent ? (
  <GlamorousComponent className={`title is-size-${mobile}-mobile 
  is-size-${tablet}-tablet is-size-${desktop}-desktop ${className}`}>
    { children } 
  </GlamorousComponent>
) : (
  <h2 className={`title is-size-${mobile}-mobile 
    is-size-${tablet}-tablet is-size-${desktop}-desktop ${className}`}>
    { children }
  </h2>
);

export default ResponsiveTitle;