import React from 'react';
import glamorous from 'glamorous';

import Popup from '../layout/Popup';
import LogoSVG from '../common-assets/logo.svg';
import QSVG from './assets/Q.svg';

const Container = glamorous.div({
  opacity: 0,
  height: '100%',
  width: '100%',
  visibility: 'hidden',
  willChange: 'transform, opacity, visibility',
  transform: 'translateX(-20%)',
  transition: 'transform 1s, opacity 1s, visibility 1s',
  backgroundImage: 'linear-gradient(135deg, #34A1DF, #1E5596)'
}, ({ menuShown }) => {
  if (menuShown) {
    return {
      opacity: 1,
      height: '100%',
      width: '100%',
      visibility: 'visible',
      transform: 'translateX(0%)'
    }
  }
});

const MobileMenuContainer = glamorous.div({
  paddingTop: '64px',
  paddingLeft: '32px'
});

const MenuLink = glamorous.a({
  display: 'block',
  fontSize: '20px',
  marginBottom: '32px',
  color: 'white'
});

const QBlock = glamorous.div({
  position: 'absolute',
  bottom: '0px',
  left: '0px',
  height: '311px',
  width: '363px',
  backgroundImage: `url(${QSVG})`,
  backgroundSize: 'cover'
});

const SmallTextBlock = glamorous.div({
  position: 'absolute',
  bottom: '0px',
  paddingLeft: '16px',
  paddingBottom: '8px',
  color: 'white',
  fontSize: '10px',
});

const MobileMenu = ({ menuShown }) => (
  <Container menuShown={menuShown}>
    <MobileMenuContainer>
      <img src={LogoSVG} />
      <hr className='is-invisible' />
      <Popup>
        <MenuLink>Transfer Now</MenuLink>
      </Popup>
      <Popup>
        <MenuLink>Get a Wallet</MenuLink>
      </Popup>
      <Popup>
        <MenuLink>How It Works</MenuLink>
      </Popup>
      <Popup>
        <MenuLink>Merchant</MenuLink>
      </Popup>
      <Popup>
        <MenuLink>Dashboard</MenuLink>
      </Popup>
      <Popup>
        <MenuLink>Others</MenuLink>
      </Popup>
    </MobileMenuContainer>
    <SmallTextBlock>
      <p>Privacy Policy</p>
      <p>Terms and Conditions</p>
      <p>&copy;2018 Copyright. All Rights Reserved.</p>
    </SmallTextBlock>
    <QBlock />
  </Container>
);

export default MobileMenu;