import React from 'react';
import glamorous from 'glamorous';

import Hero from '../Hero';
import SellingPointSection from '../SellingPointSection';
import CurvedBoxLeft from '../CurvedBox/Left';
import FirstFeatureBox from '../FeatureBox/FirstFeatureBox';
import SecondFeatureBox from '../FeatureBox/SecondFeatureBox';
import Testimonials from '../Testimonials';
import FeaturedSection from '../FeaturedSection';
import SandwichSection from '../SandwichSection';
import WhyChooseUs from '../WhyChooseUs';
import Footer from '../Footer';

import MobileMenu from '../MobileMenu';
import ThirdFeatureBox from '../FeatureBox/ThirdFeatureBox';

const MainLandingPage = glamorous.div({
  position: 'absolute',
  top: '0px',
  left: '0px',
  width: '100%',
  height: '100%',
  overflowY: 'scroll',
  WebkitOverflowScrolling: 'touch',
  willChange: 'border-radius,box-shadow',
  transition: 'border-radius 1s, box-shadow 1s',
  background: '#fff',
}, ({ menuShown }) => {
  if (menuShown) {
    return {
      borderRadius: '16px',
      overflow: 'hidden',
      boxShadow: 'rgba(0, 0, 0, 0.5) 0px 0px 80px -10px',
      zIndex: 2
    }
  }
});

const MainPageContainer = glamorous.div({
  position: 'absolute',
  top: '0px',
  left: '0px',
  height: '100%',
  width: '100%',
  transform: 'translateX(0) scale(1)',
  transition: 'transform 1s',
}, ({ menuShown }) => {
  if (menuShown) {
    return {
      transform: 'translateX(60%) scale(0.8)',
    }
  }
});

const OverlayPage = glamorous.div({
  position: 'absolute',
  background: '#000',
  opacity: '0',
  top: '0',
  right: '0',
  width: '100%',
  height: '100%',
  zIndex: 3,
  borderRadius: '16px',
  pointerEvents: 'none',
  transition: 'opacity 1s',
}, ({ menuShown }) => {
  if (menuShown) {
    return {
      opacity: ".3",
      pointerEvents: 'all',
    }
  }
});

const BlankPage = glamorous.div({
  position: 'absolute',
  top: '0',
  right: '0',
  width: '100%',
  height: '100%',
  willChange: "transform",
  borderRadius: '16px',
  backgroundColor: '#fff',
  opacity: .7,
  transform: 'translateX(0) scale(1)',
  transition: 'opacity 1s, transform 1s'
}, ({ menuShown, index }) => {
  if (menuShown) {
    return {
      zIndex: index - 1,
      transform: `translateX(${ - (index * 4) }%) scale(${ 1 - index * 0.05 })`,
    };
  }
});

class LandingPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      menuShown: false
    };

    this.toggleMenuShown = this.toggleMenuShown.bind(this);
  }

  toggleMenuShown() {
    this.setState(prev => ({ menuShown: !prev.menuShown }));
  }

  render() {
    return (
      <div>
        <MobileMenu menuShown={this.state.menuShown} />
        <MainPageContainer menuShown={this.state.menuShown}>
          <BlankPage menuShown={this.state.menuShown}
            index={2} />
          <BlankPage menuShown={this.state.menuShown}
            index={1} />
          <MainLandingPage menuShown={this.state.menuShown}>
            <Hero toggleMenuShown={this.toggleMenuShown}/>
            <FeaturedSection />
            <div className='relative'>
              <SellingPointSection />
              <WhyChooseUs />
              <SandwichSection />
            </div>
            <FirstFeatureBox />
            <SecondFeatureBox />
            <ThirdFeatureBox />
            <CurvedBoxLeft />
            <Testimonials />
            <Footer />
          </MainLandingPage>
          <OverlayPage onClick={this.toggleMenuShown} menuShown={this.state.menuShown}/>
        </MainPageContainer>
      </div>
    );
  }
}

export default LandingPage;