import glamorous from 'glamorous';

const Box = glamorous.div({
  height: '400px',
  boxShadow: 'rgba(0, 0, 0, 0.5) -5px 10px 50px -10px',
  width: '100%',
  marginLeft: '-100px',
  transform: 'skew(-20deg)'
}, ({ left, right }) => {
  return {
    borderTopLeftRadius: right ? '200px' : 0,
    borderBottomLeftRadius: right ? '200px' : 0,
    borderTopRightRadius: left ? '80px' : 0,
    borderBottomRightRadius: left ? '80px' : 0,
    backgroundImage: `linear-gradient(${ left ? -90 : 90 }deg, #20E3E4 0%, #0B537A 100%)`
  }
});

const MobileBox = glamorous.div({
  backgroundColor: '#0DC1C2',
  paddingTop: '2rem',
  paddingBottom: '2rem',
  marginTop: '2rem'
}, ({ left, right }) => ({
  transform: `skewY(${ right ? 15 : -15 }deg)`,
}));

const MobileBoxChild = glamorous.div({}, ({ left, right }) => ({
  transform: `skewY(${ right ? -15 : 15 }deg)`
}));

const ContentBox = glamorous.div({
  paddingTop: '80px',
  paddingBottom: '80px',
  marginRight: '80px',
  marginLeft: '150px',
  height: '400px',
  transform: 'skew(20deg)'
}, ({ left, right }) => ({
  paddingLeft: right ? '200px' : '80px',
}));

const NoRightPadding = glamorous.div({
  paddingRight: '0px !important'
});

const NoRightMargin = glamorous.div({
  marginRight: '0px !important'
});

const TextContainer = glamorous.p({
  maxWidth: '400px'
});

export {
  Box,
  MobileBox,
  MobileBoxChild,
  ContentBox,
  NoRightPadding,
  NoRightMargin,
  TextContainer
}