import React from 'react';
import glamorous from 'glamorous';

const Label = glamorous.h3({
  textTransform: 'uppercase',
  fontWeight: 100
})

const createFeature = (icon='fas fa-tablet-alt', label='Feature', description='It\'s awesome.') => ({
  icon,
  label,
  description
});

const features = [
  createFeature(),
  createFeature(),
  createFeature(),
  createFeature(),
];

const FeatureSet = () => (
  <div className='columns is-multiline'>
    { features.map((feature, i) => (
      <div key={i} className='column is-half'>
        <article className='media'>
          <figure className='media-left'>
            <div className='icon has-text-primary'>
              <i className={feature.icon} />
            </div>
          </figure>
          <div className='media-content'>
            <Label className='is-size-6'>{feature.label}</Label>
            <p className='is-size-6'>{feature.description}</p>
          </div>
        </article>
      </div>
    ))}
  </div>
);

export default FeatureSet;