import React from 'react';
import glamorous from 'glamorous';

import ContentSection from '../layout/ContentSection';
import Popup from '../layout/Popup';

const ImageContainer = glamorous.figure({
  marginRight: '0'
});

const FeatureBoxRight = ({ title, image, description, buttonLabel }) => (
  <ContentSection className='padded'>
    <div className='columns'>
      <div className='column has-text-right-tablet'>
        <h2 className='title'>{title}</h2>
        <p className='has-text-grey'>{description}</p>
        <hr className='is-invisible' />
        <Popup>
          <button className='button is-primary is-rounded is-medium'>
            {buttonLabel}
          </button>
        </Popup>
      </div>
      <div className='column'>
        <ImageContainer className='image is-square'>
          <img src={image} />
        </ImageContainer>
      </div>
    </div>
  </ContentSection>
);

export default FeatureBoxRight;