import React from 'react';
import glamorous from 'glamorous';

import VerticalCenterContainer from '../layout/VerticalCenterContainer';

const CircleButton = glamorous.div({
  backgroundColor: '#FFFFFF',
  height: '64px',
  width: '64px',
  borderRadius: '32px',
  marginRight: '1rem'
});

const CenterContainer = glamorous.div({
  display: 'flex',
  justifyContent: 'center'
});

const Icon = glamorous.i({}, ({ color }) => ({
  color
}));

const SocialMediaCircle = ({ icon, href, color }) => (
  <CircleButton>
    <VerticalCenterContainer>
      <CenterContainer>
        <a href={href}>
          <span className='icon'>
            <Icon className={`fab fa-${icon} fa-2x`} color={color} />
          </span>
        </a>
      </CenterContainer>
    </VerticalCenterContainer>
  </CircleButton>
);

export default SocialMediaCircle;