import React from 'react';
import glamorous from 'glamorous';
import ColoredLogo from '../common-assets/logo-coloured.svg';

import HorizontalCenterContainer from '../layout/HorizontalCenterContainer';
import Popup from '../layout/Popup';

const NavContainer = glamorous.nav({
  paddingTop: '48px',
  paddingLeft: '64px',
  paddingRight: '64px',
  '@media (max-width: 768px)': {
    paddingTop: '24px',
    paddingBottom: '0px',
    paddingRight: '32px',
    paddingLeft: '32px'
  },
});
const ImageContainer = glamorous.div({
  width: '135px',
  height: '41px',
  '@media (max-width:769px)': {
    width: '109px',
    height: '31px'
  }
});

const MobileNavContainer = glamorous.div({
  position: 'relative',
});

const MenuContainer = glamorous.a({
  position: 'absolute',
  color: 'white',
  fontSize: '24px',
  ':hover': {
    color: 'white'
  }
}, ({ whiteBackground }) => ({
  color: whiteBackground ? 'RGBA(39, 138, 211, 1.00)' : 'white',
  ':hover': {
    color: whiteBackground ? 'RGBA(39, 138, 211, 1.00)' : 'white'
  }
}));

const SignupAnchor = glamorous.a({
  position: 'absolute',
  right: '0',
  top: '0',
  color: 'rgba(255,255,255,0.7)',
  padding: '8px',
  fontWeight: '700',
  ':hover': {
    color: 'rgba(255,255,255,1)'
  }
}, ({ whiteBackground }) => ({
  color: whiteBackground ? 'RGBA(39, 138, 211, 1.00)' : 'rgba(255,255,255,0.7)',
  ':hover': {
    color: whiteBackground ? 'RGBA(39, 138, 211, 1.00)' : 'rgba(255,255,255,1)'
  }
}));

const SpaceBetween = glamorous.div({
  display: 'flex',
  justifyContent: 'space-between',
});

const MenuColumns = glamorous.div({
  width: '50%'
});

const NavBarLink = glamorous.a({
  color: 'white',
  ':hover': {
    color: 'white'
  }
})

const NavBar = ({ toggleMenuShown }) => (
  <NavContainer role='navigation' aria-label='main navigation'>
    <MobileNavContainer className='is-hidden-desktop'>
      <MenuContainer 
        className='icon is-medium'
        onClick={toggleMenuShown}>
        <i className='fas fa-bars fa-2x fa-black' />
      </MenuContainer>
      <HorizontalCenterContainer>
        <ImageContainer>
          <img src={ColoredLogo} alt='Linqcash' />
        </ImageContainer>
      </HorizontalCenterContainer>
      <Popup>
        <SignupAnchor className='is-hidden-desktop'>Signup</SignupAnchor>
      </Popup>
    </MobileNavContainer>
    <SpaceBetween className='is-hidden-touch is-fluid'>
      <ImageContainer>
        <img src={ColoredLogo} alt='Linqcash' />
      </ImageContainer>
      <MenuColumns className='columns has-text-right'>
        <div className='column'>
          <Popup>
            <NavBarLink href='#'>
              <button className='button is-primary is-rounded'>
                Transfer Now
              </button>
            </NavBarLink>
          </Popup>
        </div>
        <div className='column'>
          <Popup>
            <NavBarLink href='#'>
              Get A Wallet
            </NavBarLink>
          </Popup>
        </div>
        <div className='column'>
          <Popup>
            <NavBarLink href='#'>
              Merchant
            </NavBarLink>
          </Popup>
        </div>
        <div className='column'>
          <Popup>
            <NavBarLink href='#'>
              Dashboard
            </NavBarLink>
          </Popup>
        </div>
      </MenuColumns>
    </SpaceBetween>
  </NavContainer>
);

export default NavBar;